@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif, Arial !important;
  font-weight: normal;
  font-style: normal;
  background-color: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
  background-color: #d8dee9;
}

/* unvisited link */
b a:link,
p a:link {
  color: #274972;
}

/* visited link */
b a:visited,
p a:visited {
  color: #274972;
}

/* mouse over link */
b a:hover,
p a:hover {
  color: #274972;
}

/* selected link */
b a:active,
p a:active {
  color: #274972;
}

* {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
::-webkit-scrollbar {
  display: none;
}



.icons-root {
    float: right;
    margin-top: 0.5%;
    margin-right: 0.5%;
}
.animated-name-text {
  position: relative;
  width: 100%;
  height: 60px;
  line-height: 60px;
  will-change: transform, opacity;
  overflow: hidden;
}

.animated-name-text > div {
  overflow: hidden;
}

.layout-root {
  background-color: #3b4252 !important;
}

.header-desktop {
  border-radius: 0 !important;
  height: inherit;
  display: flex;
  padding: 2%;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none; /* Chrome all / Safari all */ /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
}

.header-mobile {
  border-radius: 0 !important;
  height: inherit;
  display: flex;
  padding: 2%;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none; /* Chrome all / Safari all */ /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
}

.header-text {
  color: #d8dee9;
  padding-left: 10%;
  margin: auto 0;
}

.profile-image-desktop {
  height: 20%;
  width: 20%;
}

.profile-image-mobile {
  height: 50%;
  width: 50%;
}

.content {
  padding-top: 10%;
  text-align: left;
}

.info-body {
  font-size: 18px;
}

.para-end {
  margin-bottom: 3%;
}

.content-heading {
  margin-bottom: 3%;
}




/* Hide scrollbar for Chrome, Safari and Opera */




.card {
    opacity: 0.6;
    transition: 0.3s;
    display: inline-block;
    color: black;
    width: inherit;
    margin-bottom: 20px;
  }
  
  .webcard-link {
    text-decoration: none !important;
  }
  
  .card:hover {
    opacity: 1;
    transform: translateY(2px);
  }
.input-content {
  resize: none;
  border-radius: 2px;
  padding: 4px;
  border: none;
  outline: none;
}

.input-name {
  padding: 4px;
  border: none;
  outline: none;
  margin-bottom: 20px;
}

.posted-message {
  width: inherit;
  margin-top: 10px;
}

.submit-btn {
  background-color: #3b4252 !important;
  border-radius: 4px;
  border: none;
  color: #d8dee9;
  text-decoration: none;
  padding: 8px 20px;
  font-size: 20px;
  border-style: none;
  margin-bottom: 20px;
}

.submit-btn:focus {
  outline: 0;
}

.list-group {
  max-height: 500px;
  overflow: auto;
}

